Initialization/Defaults
--------------------------------------------*/
/* --- Block Elements --- */
html {
  width: 100%;
  height: 100%;
  background: #fff;
  font-size: 62.5%;
  color: #000; }

/* 62.5% = 10px = 1.0rem */
a img {
  border: none; }

/* --- Block Text --- */
div {
  margin: 0; }

/* Localize if necessary */
/* --- Text Elements --- */
a {
  color: #000;
  text-decoration: underline; }

a:hover,
a:focus,
a:active {
  text-decoration: none; }

strong {
  font-weight: bold; }

em {
  font-style: italic; }

/* --- Form/Inputs --- */
form {
  margin: 0;
  padding: 0; }

fieldset {
  margin: 0;
  padding: 0;
  border: 0; }

legend {
  color: #000; }

label {
  display: block;
  font-size: 14px;
  font-size: 1.4rem;
  color: #000;
  cursor: pointer; }

input[type="text"] {
  margin-bottom: 0;
  padding: 3px 5px;
  border: 2px solid #ccc;
  font-size: 15px;
  font-size: 1.5rem;
  color: #000; }

input[type="radio"],
input[type="checkbox"] {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  vertical-align: middle; }

input[type="text"][disabled] {
  background: #fff; }

::-webkit-input-placeholder {
  color: #000; }

::-moz-placeholder {
  color: #000; }

/* firefox 19+ */
:-ms-input-placeholder {
  color: #000; }

/* ie */
input:-moz-placeholder {
  color: #000; }

/* 
     Utility ID's
     ----------------------- */
.clear {
  display: block;
  clear: both;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: 0 -1px -1px 0;
  border: 0;
  font-size: 0;
  line-height: 0; }

.hide, .ui-countryselector legend {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 0 !important;
  height: 0; }

/*
Country Selector UI
--------------------------------------------*/
/* Auto Complete */
#autoCountry {
  position: absolute; }

.quoteWidget .ui-autocomplete {
  top: auto !important;
  left: auto !important;
  overflow-x: hidden;
  overflow-y: scroll;
  height: 200px;
  margin: 35px 0 0;
  padding: 0;
  box-sizing: border-box; }

.quoteWidget .ui-menu-item {
  position: relative;
  margin: 0;
  padding: 0;
  background: transparent none;
  border: 0;
  cursor: default;
  min-height: 0;
  list-style-image: none; }

.quoteWidget .ui-menu button,
.quoteWidget .ui-menu button:hover,
.quoteWidget .ui-menu button:focus,
.quoteWidget .ui-menu button:active {
  width: 100%;
  height: 30px;
  padding: 0 0 0 30px;
  background: transparent none !important;
  border: 0 !important;
  box-sizing: border-box;
  cursor: pointer;
  text-align: left; }

.quoteWidget .ui-menu button:hover,
.quoteWidget .ui-menu button:focus,
.quoteWidget .ui-menu button:active {
  text-decoration: underline; }

/*
  Country/Region Selector 
-----------------------------*/
/* Display Field */
#country {
  overflow: hidden;
  position: relative;
  z-index: 5;
  float: left;
  display: block;
  width: 100%;
  height: 34px;
  margin: 0;
  padding: 5px 35px 0 89px;
  border: 2px solid #ccc;
  font-size: 1.6rem;
  color: #000;
  vertical-align: middle;
  text-overflow: ellipsis;
  white-space: nowrap;
  box-sizing: border-box; }

label[for="country"] {
  position: relative;
  top: 7px;
  left: 11px;
  z-index: 6;
  width: 70px;
  margin: 1px 0 -20px;
  font-size: 1.5rem;
  cursor: default; }

/* Selector Button */
.countrySelector {
  overflow: hidden;
  position: absolute;
  z-index: 5;
  float: right;
  width: 22px;
  height: 22px;
  margin: 6px 0 0 -28px;
  background: url(../files/images/backgrounds/bkg-country.png) no-repeat 50% 50%;
  background-size: 22px auto;
  border: 0;
  cursor: pointer;
  text-indent: -300px; }
  .countrySelector.countrySelector.openCountry {
    display: none; }

/* Close Window */
.ui-countryselector .closeWindow {
  overflow: hidden;
  position: relative;
  top: -78px;
  left: 2px;
  float: right;
  display: block;
  width: 60px;
  height: 30px;
  margin: 0 0 0;
  padding: 3px 0 0;
  background: #000;
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: bold;
  color: #fff !important;
  text-align: center;
  text-decoration: none; }
  .ui-countryselector .closeWindow:hover, .ui-countryselector .closeWindow:focus, .ui-countryselector .closeWindow:active {
    text-decoration: none; }

/* Display Grid */
.ui-countryselector {
  position: absolute;
  top: 70px;
  z-index: 4;
  width: 282px;
  height: 200px;
  margin: -13px 0 0 -4px;
  padding: 45px 0 0;
  background: #fff;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
  border: 1px solid #ccc; }

.ui-countryselector .groups-container {
  overflow: auto;
  position: absolute;
  width: 100%;
  height: 200px;
  margin: 0;
  padding: 0;
  background: #ddd;
  border: 0;
  border-top: 1px solid #b1b1b1;
  -webkit-overflow-scrolling: touch; }

/* Region/Country Grouping */
.ui-countryselector .group {
  width: 100%; }
  .ui-countryselector .group.active {
    z-index: 1000; }

/* Labels */
.ui-countryselector .group-label {
  margin: 0;
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #b1b1b1;
  -webkit-user-select: none;
  box-sizing: border-box; }
  .ui-countryselector .group-label strong {
    display: block;
    height: 49px;
    padding: 15px 15px 0;
    background: #fff none;
    font-weight: normal;
    font-size: 1.6rem;
    box-sizing: border-box; }

.ui-countryselector .group input[type="radio"]:checked + .group-label {
  background: #fff; }

.ui-countryselector .group input[type="radio"]:checked + .group-label strong {
  background-position: -70px 0;
  font-weight: bold;
  text-shadow: none; }

/* Opening Regions */
input[type="radio"] + label ~ div.columns {
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  width: 100%;
  height: 0;
  margin: 0; }

input[type="radio"]:checked + label ~ div.columns {
  position: static;
  height: auto; }

/* Columns */
.ui-countryselector .column {
  overflow: hidden;
  float: none;
  position: static;
  display: block;
  width: 100%;
  margin: 0;
  padding: 0; }

.ui-countryselector .column input[type="radio"] {
  margin-left: 20px;
  z-index: 1; }

.ui-countryselector .column label {
  height: 50px;
  margin: 0;
  padding: 13px 15px 0 25px;
  border-bottom: 1px solid #b1b1b1;
  border-radius: 0;
  font-size: 1.5rem;
  -webkit-user-select: none;
  box-sizing: border-box; }

.ui-countryselector .group-label:hover strong {
  font-weight: normal; }

.ui-countryselector .group input[type="radio"]:checked + .group-label strong {
  /*background: #46c8fb none; font-weight: bold;*/ }

.ui-countryselector .column input[type="radio"]:checked + label {
  background: #09c none;
  color: #fff; }

/* -------------------------------- [ Breakpoints ] */
/* Desktop */
@media only screen and (min-width: 980px) {
  /* Display Field */
  .ui-countryselector .closeWindow {
    top: -73px;
    left: 13px;
    width: 22px;
    height: 19px;
    font-size: 1.3rem; }
  /* Display Grid */
  .ui-countryselector {
    top: 70px;
    width: 925px;
    height: 386px;
    min-height: 0;
    margin-left: -13px;
    padding-top: 58px; }
  .ui-countryselector .groups-container {
    height: 385px;
    background: transparent url(../files/images/backgrounds/bkg-nav-regions.png) no-repeat;
    background-size: 170px 390px;
    border-top-color: #ddd;
