  /* Region/Country Grouping */
  .ui-countryselector .group {
    position: absolute;
    left: 170px;
    width: 730px; }
  .ui-countryselector .group-label {
    position: absolute;
    display: block;
    margin-left: -170px;
    width: 170px;
    height: 34px;
    border-bottom: 1px solid #b1b1b1; }
  .ui-countryselector .group-label strong {
    float: left;
    width: 150px;
    height: 22px;
    padding: 12px 20px 0 10px;
    background: transparent none;
    font-weight: normal;
    font-size: 1.1rem;
    line-height: 1.2;
    color: #292929;
    text-shadow: 1px 1px 1px white;
    box-sizing: border-box; }
  .ui-countryselector .group input[type="radio"]:checked + .group-label {
    background: #fff; }
  .ui-countryselector .group input[type="radio"]:checked + .group-label strong {
    background-position: -70px 0;
    font-weight: bold;
    text-shadow: none; }
  /* Columns */
  .ui-countryselector .column {
    position: relative;
    z-index: 10;
    column-count: 4; }
  .ui-countryselector .column li {
    margin: 0 0 0 10px;
