  .region-3 {
    top: 70px; }
  .region-4 {
    top: 105px; }
  .region-5 {
    top: 140px; }
  .region-6 {
    top: 175px; }
  .region-7 {
    top: 210px; }
  .region-8 {
    top: 245px; }
  .region-9 {
    top: 280px; }
  .region-10 {
    top: 315px; } }
